.miniature {
    width: 300px;
    min-height: 340px;
    max-height: 340px;
    margin: 2.5rem 3.5rem 3rem;
}
.miniature.similar {
    margin: 0 0 3.5rem;
}
.min-article-wrapper{
    padding: 1rem;
    overflow: hidden;
    height: 310px;
    position: relative;
}
.miniature img {
    max-width: 100%;
    max-height: 75%;
    min-height: 20%;
    width: auto;
    margin-top: 1rem;
}

.title {
    min-height: 60px;
}

.slide {
    overflow-y: hidden;
    max-height: 215px; /* approximate max height */
    width: 100%;

    -webkit-transition-property: max-height;

    -o-transition-property: max-height;

    transition-property: max-height;
    -webkit-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
    -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
         -o-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
            transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}

.description-miniature {
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
}
.miniature:hover .description-miniature {
    white-space: normal;
}

.button-consult:hover {
    background-color: rgba(141, 110, 99, 0.08) !important;
}

.icon-with-value {
    padding-top: 3px;
    margin-right: 2px;
}

.comments {
    width: 100%;
    margin-top: 4rem;
}

.tag-list-min-article {
    display: -ms-flexbox;
    display: flex;
    overflow-x: hidden;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    margin-top: 5px;
}

.folder-info {
    position: absolute;
    top: 0;
    left: 0;
    color: black;
}

.icon-miniature {
    padding-top: 1px;
    padding-left: 1px;
    padding-right: 5px;
}

.date-miniature {
    position: absolute;
    top: 2px;
    right: 0;
    color: black;
}

.miniature-infos {
    font-size: 0.8125rem;
}


