.App {
  text-align: center;
  background-color: #eeeeee;
  min-height: 100vh;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

p {
  text-align: justify;
  text-justify: inter-word;
}
.app-content {
  min-height: 100vh;
}
.app-footer {
  background-color: #ddd;
  padding: 1rem;
}
.rotate-180 {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.header-logo {
  max-height: 50px;
}

.app-parent {
  min-height: 100vh;
  padding-bottom: 5rem;
}

.site-list {
    margin-top: 30px;
}
.site-list img {
    padding: 50px;
}
.site-list span {
    cursor: pointer;
}

.site-list-2 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}


.gdprBanner {
    position: fixed;
    bottom: 0;
    background-color: #cccccc;
    opacity: 0.9;
    min-height: 130px;
    width: 100%;
    padding: 1rem 5rem;
    z-index: 1000;
}

.gdprBanner > button {
    margin-top: 1rem;
}

@media (max-height: 1000px) {
    .site-list-2 {
        margin-top: 30px;
    }
}
@media (max-width: 1100px) {
    .gdprBanner {
        padding: 1rem 1rem;
    }
}


